              </el-form-item>
              <el-form-item label="開標範圍">
                <span>{{ props.row.bid_range }}</span>
              </el-form-item>
              <el-form-item label="發佈時間">
                <span>{{ props.row.publish_date }}</span>
              </el-form-item>
              <el-form-item label="交貨地點">
                <span>{{ props.row.deliver_address }}</span>
              </el-form-item>
              <el-form-item label="交貨時間">
                <span>{{ props.row.deliver_date }}</span>
              </el-form-item>
              <el-form-item label="聯繫電話">
                <span>{{ props.row.tel }}</span>
              </el-form-item>
              <el-form-item label="競標開始時間">
                <span>{{ props.row.bid_start_date }}</span>
              </el-form-item>
              <el-form-item label="貨幣種類">
                <span>{{ props.row.money_type }}</span>
              </el-form-item>
              <el-form-item label="競標結束時間">
                <span>{{ props.row.bid_end_date }}</span>
              </el-form-item>
              <el-form-item label="交貨方式">
                <span>{{ props.row.deliver_way }}</span>
              </el-form-item>
              <el-form-item label="製作要求">
                <span>{{ props.row.make_requ }}</span>
              </el-form-item>
            </el-form>
            <p style="margin-top:2rem;color:#0092ff;">發單賬號信息</p>
            <el-form
              label-position="left"
              label-width="120px"
              inline
              class="demo-table-expand"
            >
              <el-form-item label="發單用戶">
                <span>{{ props.row.send_user.username }}</span>
              </el-form-item>
              <el-form-item label="聯繫電話">
                <span>{{ props.row.send_user.tel }}</span>
              </el-form-item>
              <el-form-item label="廠區">
                <span>{{ props.row.send_user.fctry_zone }}</span>
              </el-form-item>
              <el-form-item label="聯繫手機">
                <span>{{ props.row.send_user.phone }}</span>
              </el-form-item>
              <el-form-item label="次集團">
                <span>{{ props.row.send_user.secn_cmpy }}</span>
              </el-form-item>
              <el-form-item label="Email">
                <span>{{ props.row.send_user.email }}</span>
              </el-form-item>
              <el-form-item label="事業群">
                <span>{{ props.row.send_user.entrps_group }}</span>
              </el-form-item>
              <el-form-item label="創建時間">
                <span>{{ props.row.send_user.create_date }}</span>
              </el-form-item>
              <el-form-item label="產品處">
                <span>{{ props.row.send_user.pd_office }}</span>
              </el-form-item>
              <el-form-item label="上次登錄時間">
                <span>{{ props.row.send_user.latest_login_time }}</span>
              </el-form-item>
              <el-form-item label="單位名稱">
                <span>{{ props.row.send_user.dept_name }}</span>
              </el-form-item>
              <el-form-item label="單位代碼">
                <span>{{ props.row.send_user.dept_code }}</span>
              </el-form-item>
              <el-form-item label="法人">
                <span>{{ props.row.send_user.legal_person }}</span>
              </el-form-item>
              <el-form-item label="費用代碼">
                <span>{{ props.row.send_user.cost_code }}</span>
              </el-form-item>
              <el-form-item label="業務窗口">
                <span>{{ props.row.send_user.busis_mngr }}</span>
              </el-form-item>
              <el-form-item label="簡介">
                <span>{{ props.row.send_user.summary }}</span>
              </el-form-item>
            </el-form>
            <p style="margin-top:2rem;color:#0092ff;">接單賬號信息</p>
            <el-form
              v-if="props.row.recv_user"
              label-position="left"
              label-width="120px"
              inline
              class="demo-table-expand"
            >
              <el-form-item label="發單用戶">
                <span>{{ props.row.recv_user.username }}</span>
              </el-form-item>
              <el-form-item label="聯繫電話">
                <span>{{ props.row.recv_user.tel }}</span>
              </el-form-item>
              <el-form-item label="廠區">
                <span>{{ props.row.recv_user.fctry_zone }}</span>
              </el-form-item>
              <el-form-item label="聯繫手機">
                <span>{{ props.row.recv_user.phone }}</span>
              </el-form-item>
              <el-form-item label="次集團">
                <span>{{ props.row.recv_user.secn_cmpy }}</span>
              </el-form-item>
              <el-form-item label="Email">
                <span>{{ props.row.recv_user.email }}</span>
              </el-form-item>
              <el-form-item label="事業群">
                <span>{{ props.row.recv_user.entrps_group }}</span>
              </el-form-item>
              <el-form-item label="創建時間">
                <span>{{ props.row.recv_user.create_date }}</span>
              </el-form-item>
              <el-form-item label="產品處">
                <span>{{ props.row.recv_user.pd_office }}</span>
              </el-form-item>
              <el-form-item label="上次登錄時間">
                <span>{{ props.row.recv_user.latest_login_time }}</span>
              </el-form-item>
              <el-form-item label="單位名稱">
                <span>{{ props.row.recv_user.dept_name }}</span>
              </el-form-item>
              <el-form-item label="單位代碼">
                <span>{{ props.row.recv_user.dept_code }}</span>
              </el-form-item>
              <el-form-item label="法人">
                <span>{{ props.row.recv_user.legal_person }}</span>
              </el-form-item>
              <el-form-item label="費用代碼">
                <span>{{ props.row.recv_user.cost_code }}</span>
              </el-form-item>
              <el-form-item label="業務窗口">
                <span>{{ props.row.recv_user.busis_mngr }}</span>
              </el-form-item>
              <el-form-item label="加工範圍">
                <span>{{ props.row.recv_user.recv_range_list_str }}</span>
              </el-form-item>
              <el-form-item label="簡介">
                <span>{{ props.row.recv_user.summary }}</span>
              </el-form-item>
            </el-form>
            <div v-else>暫無</div>
          </template>
        </el-table-column>
        <el-table-column label="單號" prop="bill_no"> </el-table-column>
        <el-table-column label="狀態" prop="bill_status_zh"> </el-table-column>
        <el-table-column label="發單用戶" prop="send_user.busis_mngr">
        </el-table-column>
        <el-table-column label="中標用戶" prop="recv_user.busis_mngr">
        </el-table-column>
        <el-table-column label="接受總價" prop="total_price"> </el-table-column>
        <el-table-column label="中標總價" prop="give_price.total_price">
        </el-table-column>
        <el-table-column label="時間">
          <!-- prop="publish_date" -->
          <template slot-scope="scope">
            {{ scope.row.publish_date }}
          </template>
        </el-table-column>
      </el-table>
      <div class="paging">
        <m-paging
          :total="total"
          :current-page="currentPage"
          :page-size="pageSize"
          @getCurrentPage="getListDate"
        />
      </div>
    </div>
  </div>
</template>

<script>
import MPaging from "@/components/paging/Paging";
import MInfo from "./Modules/Info";
import MFilt from "./Modules/Filt";
import { query_bill_list_not_login } from "../../../api/order";

export default {
  data() {
    return {
      // 分頁數據
      total: 60,
      currentPage: 1,
      pageSize: 11,
      //
      tableData: [],
      // 查詢條件
      filtData: {
        bill_status: "",
        bill_no: "",
        send_user_name: "",
        recv_user_name: ""
      }
    };
  },
  created() {
    let page = Number(sessionStorage.getItem("adminOrderSummary"));
    page = page ? page : 1;
    this.getListDate(page);
  },
  components: {
    MPaging,
    MInfo,
    MFilt
  },
  filters: {
    bidStatus: val => {}
  },
  methods: {
    // 獲取filt數據
    getFiltData(value) {
      for (let item of value) {
        switch (item.name) {
          case "bill_status":
            this.filtData.bill_status = item.val;
            break;
          case "bill_no":
            this.filtData.bill_no = item.val;
            break;
          case "send_user_name":
            this.filtData.send_user_name = item.val;
            break;
          case "recv_user_name":
            this.filtData.recv_user_name = item.val;
            break;
          default:
            break;
        }
      }
      console.log('this.filtData.bill_status', this.filtData.bill_status)
      this.loadingData()
    },
    // 處理Data
    handleData(data) {
      for (let item of data) {
        switch (item.bill_status) {
          case "-2":
            item.bill_status_zh = "已棄標";
            break;
          case "-1":
            item.bill_status_zh = "已流標";
            break;
          case "0":
            item.bill_status_zh = "待發佈";
            break;
          case "1":
            item.bill_status_zh = "待報價";
            break;
          case "2":
            item.bill_status_zh = "待發貨";
            break;
          case "3":
            item.bill_status_zh = "待收貨";
            break;
          case "4":
            item.bill_status_zh = "待付款";
            break;
          case "5":
            item.bill_status_zh = "待收款";
            break;
          case "6":
            item.bill_status_zh = "已完成";
            break;
        }
        if (item.recv_user) {
          let rangeListRes = item.recv_user.recv_range_list;
          let rangeList = "";
          for (let range of rangeListRes) {
            rangeList = rangeList + range.name + "、";
          }
          item.recv_user.recv_range_list_str = rangeList;
        }
      }
      this.tableData = data;
    },
    // 加載Data
    loadingData() {
      const data = {
        bid_range: "集團",
        deliver_address: "all",
        deliver_date: "all",
        pd_type: "all",
        pageIndex: this.currentPage,
        pageSize: this.pageSize,
        bill_status: this.filtData.bill_status || 'all',
        bill_no: this.filtData.bill_no,
        send_user_name: this.filtData.send_user_name,
        recv_user_name: this.filtData.recv_user_name,
      };
      query_bill_list_not_login(data).then(res => {
        if (res.code === "1") {
          this.total = res.t.row_total;
          this.handleData(res.t.bill_list);
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    // 獲取數據
    getListDate(page = 1) {
      this.currentPage = Number(page);
      sessionStorage.setItem("adminOrderSummary", this.currentPage);
      this.loadingData();
    }
  }
};
</script>

<style lang="less">
.order-summary {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  .info-box,
  .table-box {
    background-color: #eff9ff;
    box-sizing: border-box;
    padding: 20px;
  }
  .table-box {
    margin-top: 10px;
    flex: 1;
  }
  .paging {
  }
  // table
  .el-table th,
  .el-table tr {
    background-color: #eff9ff;
  }
  .el-table__expanded-cell {
    background-color: rgba(255, 255, 255, 0.4);
  }
  .el-table__expanded-cell:hover {
    background-color: #fff !important;
  }
  .el-table .el-table__row:hover td {
    background-color: #e1e9ef;
  }
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
}
</style>
